import Image, { StaticImageData } from "next/image";

const Imagem = ({ src, alt }: { src: StaticImageData; alt?: string }) => {
  return (
      <Image
        src={src}
        alt={alt || ""}
        fill
        className="w-full h-auto object-cover"
        priority
      />
  );
};

export default Imagem;
