import Imagem from "../../components/Imagem";
import heroImg from "../../assets/bg-image-hero.png";

export const Hero = () => {
  return (
    <>
    <div className="relative h-screen w-full overflow-hidden">
      <Imagem
        src={heroImg}
        alt="Hero Image"
      />
    </div>
    </>
  );
};

export default Hero;
